* {
  margin: 0%;
  padding: 0%;
}

body {
  background-color: #fff;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.titulo {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #260052;
  width: 100%;
  height: 6vh;
}

.box {
  background-color: #acbe94;
  border: 2px solid #000;
  border-radius: 10px;
  grid-template-areas: "name name"
                       "side1 side2";
  justify-content: space-between;
  width: 70%;
  margin: 4% auto;
  display: grid;
}

.box > h1 {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #132222;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  grid-area: name;
  height: 6vh;
  font-size: 2rem;
}

.box > .side {
  padding: 10px;
}

canvas {
  width: 100%;
  height: 100%;
}

.box .titulo-conteudo {
  margin: 1% 0 .5% 2.5%;
}

.box p {
  margin: 2% 0 1% 5%;
  font-size: 1.5rem;
  font-weight: 100;
}

.box li {
  margin: 0 0 1% 7%;
  font-size: 1rem;
  font-weight: 100;
}

.footer {
  color: #1e1e2a;
  text-align: center;
  width: 100%;
  height: 100px;
}

